import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import Nav from '../m/nav.jsx';
import Menu from '../m/menu.jsx';

const styles = {
  card: {
    margin: '16px'
  },
  cardHeader: {
    lineHeight: '1.5em'
  },
  content: {
    marginTop: '80px',
    marginBottom: '88px'
  }
};

const Bulletin = React.createClass({
  render() {
    const bulletin  = this.props.bulletin;
    const time = new Date(bulletin.time).format('yyyy-MM-dd hh:mm');
    return (
      <Card style={styles.card}>
        <CardHeader
          title={bulletin.title}
          subtitle={time}
          actAsExpander={true}
          showExpandableButton={true}
          style={styles.cardHeader}
          />
        <CardText expandable={true}>
          {bulletin.content}
        </CardText>
      </Card>
    )
  }
});

const windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
const Index = React.createClass({
  getInitialState() {
    return {
      bulletinList: [], //
      isLoading: false, //
      isEnd: false  //
    }
  },
  /**
   *  handleScroll 
   * @return {[type]} [description]
   */
  componentDidMount(){
    var self = this;
    window.addEventListener('scroll', this.handleScroll, false);
    this.handleScroll();
  },
  /**
   *  handleScroll 
   * @return {[type]} [description]
   */
  componentWillUnmount(){
    window.removeEventListener('scroll', this.handleScroll, false);
  },
  /**
   * 
   * @return {[type]} [description]
   */
  handleScroll(){
    var self = this;
    var scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
    var docHeight = document.body.scrollHeight ;
    if (!self.state.isLoading && !self.state.isEnd && docHeight - scrollTop - windowHeight < 100) {
      this.setState({
        isLoading: true
      });
      fetch('http://192.168.31.111:10005/back/index?size=20')
      .then(function(res) {
        return res.json();
      })
      .then(function(res){
        self.setState({
          isLoading: false,
          isEnd: res.data.length < 20,
          bulletinList: [...self.state.bulletinList, ...res.data]
        });
      })
      .catch(err => {
        alert(err)
      });
    }
  },
  /**
   * 
   * @return {[type]} [description]
   */
  render() {
    const bulletinList = this.state.bulletinList.map((bulletin, index) => (
      <Bulletin bulletin={bulletin} key={index}/>
    ));
    return (
      <div>
        <Nav title=""/>
        <div style={styles.content}>{bulletinList}</div>
        <Menu index={0}/>
      </div>
    )
  }
});

export default Index;



